import axios from "axios";
import goods from "./mockPhones";

import R from "ramda";

export const fetchGoods = async () => {
  return axios
    .get("https://thawing-basin-83739.herokuapp.com/index.php?r=api%2Fget-goods")
    .then(res => res.data);
};

export const fetchGoodById = async id => {
  return new Promise((resolve, reject) => {
    const good = R.find(R.propEq("id", id), goods);
    resolve(good);
  });
};



