import React, { Component } from "react";
import { connect } from "react-redux";
import R from "ramda";
import { Link } from "react-router";

import { getPhoneById } from "selectors";
import { fetchGoodById } from "actions/goods";
import { addPhoneToBasket } from "actions";
import BasketCart from "components/basketCart";
import Header from "components/Header";

class Phone extends Component {
  componentDidMount() {
    this.props.fetchGoodById(this.props.params._id);
  }

  renderFields() {
    const { good } = this.props;
    const columnFields = R.compose(
      R.toPairs,
      R.pick(["name", "weight", "price", "description", "material"])
    )(good);

    return columnFields.map(([key, value]) => (
      <div className="column" key={key}>
        <div className="ab-details-title">
          <p>{key}</p>
        </div>
        <div className="ab-details-info">{value}</div>
      </div>
    ));
  }

  renderContent() {
    const { good } = this.props;

    return (
      <div className="thumbnail">
        <div className="row">
          <div className="col-md-6">
            <img className="img-thumbnail" src={good.file} alt={good.name} />
          </div>
          <div className="col-md-6">{this.renderFields()}</div>
        </div>
        <div className="caption-full">
          <h4 className="pull-right">${good.price}</h4>
          <h4>{good.name}</h4>
          <p>{good.description}</p>
        </div>
      </div>
    );
  }

  renderSidebar() {
    const { good, addPhoneToBasket } = this.props;
    return (
      <div>
        <p className="lead">Quick shop</p>
        <BasketCart />
        <div className="form-group">
          <h1>{good.name}</h1>
          <h2>${good.price}</h2>
        </div>
        <Link to="/" className="btn btn-info btn-block">
          Вернуться в магазин
        </Link>
        <button
          type="button"
          className="btn btn-success btn-block"
          onClick={() => addPhoneToBasket(good._id)}
        >
          Добавить в корзину
        </button>
      </div>
    );
  }

  render() {
    const { good } = this.props;
    return (
      <div className="view-container">
        <Header />
        <div className="container">
          <div className="row">
            <div className="col-md-9">{good && this.renderContent()}</div>
            <div className="col-md-3">{good && this.renderSidebar()}</div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const good = state.goods.filter(item => item._id === state.goodPage._id);
  return {
    good: good[0]
  };
};

const mapDispatchToProps = {
  fetchGoodById,
  addPhoneToBasket
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Phone);



