import R from "ramda";
import { FETCH_NEWS_SUCCESS } from "actionTypes";

const initialState = [];

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case FETCH_NEWS_SUCCESS:
      const newState = [...payload];
      return newState;
    default:
      return state;
  }
};



