import R from "ramda";

import { FETCH_GOOD_BY_ID_SUCCESS } from "actionTypes";

const initialState = {
  _id: null
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case FETCH_GOOD_BY_ID_SUCCESS:
      return Object.assign({}, state, {
        _id: payload
      });
    default:
      return state;
  }
};



