import {
  FETCH_NEWS_START,
  FETCH_NEWS_SUCCESS,
  FETCH_NEWS_FAILURE
} from "actionTypes";

import { fetchNews as fetchNewsApi } from "api/news";

export const fetchNews = () => async dispatch => {
  dispatch({ type: FETCH_NEWS_START });

  try {
    const news = await fetchNewsApi();
    console.log("news", news);
    dispatch({
      type: FETCH_NEWS_SUCCESS,
      payload: news
    });
  } catch (err) {
    dispatch({
      type: FETCH_NEWS_FAILURE,
      payload: err,
      error: true
    });
  }
};



