import React from "react";

import "./index.css";

const Footer = () => (
  <div id="footer">
    <footer className="page-footer">
      <div className="container">
        <div className="row">
          <div className="col-xs-12 social-links">
            <a className="fb-ic link" href="https://facebook.com" target="_blank">
              <i className="fab fa-facebook" />
            </a>
            <a className="tw-ic link" href="https://instagram.com" target="_blank">
              <i className="fab fa-instagram" />
            </a>
            <a className="gplus-ic link" href="https://twitter.com" target="_blank">
              <i className="fab fa-twitter" />
            </a>
          </div>
        </div>
        <div className="footer-copyright">© 2018</div>
      </div>
    </footer>
  </div>
);

export default Footer;



