import axios from "axios";
import categories from "./mockCategories";

export const fetchCategories = async () => {
  return axios
    .get(
      "https://thawing-basin-83739.herokuapp.com/index.php?r=api%2Fget-categories"
    )
    .then(res => res.data);
};



