import React from 'react'
import {Link} from 'react-router'

const NoMatch = () => (
    <div>
       <div className="jumbotron" style={{minHeight: '100vh'}}>
            <h1>Шо вы здесь забыли?</h1>
            <p>
                <Link className="btn btn-primary btn-lg" to='/'>
                    Выйти
                </Link>
            </p>
        </div>
    </div>
)

export default NoMatch


