import React, { Component } from "react";
import { connect } from "react-redux";
import R from "ramda";
import { Link } from "react-router";

import {
  fetchPhones,
  loadMorePhones,
  addPhoneToBasket,
  fetchCategories
} from "actions";

import { fetchGoods } from "actions/goods";

class Phones extends Component {
  componentDidMount() {
    this.props.fetchGoods();
    this.props.fetchCategories();
  }

  renderItem(item, index) {
    const { addPhoneToBasket } = this.props;
    const shortDescription = `${R.take(60, item.description)}...`;

    return (
      <div className="col-sm-4 col-lg-4 col-md-4 book-list" key={index}>
        <div
          className="thumbnail"
          style={{
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "space-between"
          }}
        >
          <Link to={`/goods/${item._id}`} style={{ margin: "auto" }}>
            <img className="img-thumbnail" src={item.file} alt={item.name} />
          </Link>
          <div className="caption">
            <h4 className="pull-right">${item.price}</h4>
            <h4>
              <Link to={`/goods/${item._id}`}>{item.name}</Link>
            </h4>
            <p>{shortDescription}</p>
            <p className="itemButton">
              <button
                className="btn btn-primary"
                onClick={() => addPhoneToBasket(item._id)}
              >
                Добавить в корзину
              </button>
            </p>
          </div>
        </div>
      </div>
    );
  }

  render() {
    const { goods } = this.props;
    return (
      <div>
        <div
          className="books row"
          style={{
            display: "flex",
            flexWrap: "wrap"
          }}
        >
          {goods.map((item, index) => this.renderItem(item, index))}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    goods: state.goods
  };
};

const mapDispatchToProps = {
  fetchGoods,
  loadMorePhones,
  addPhoneToBasket,
  fetchCategories
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Phones);



